/*eslint-disable*/
import { Link } from 'react-router-dom';
import './navbar.scss';
import * as FaIcons from 'react-icons/fa';
import * as AiIcons from 'react-icons/ai';
import React, { useState } from 'react';
import Switcher from '../switch/Switcher';
import { useTypedSelector } from '../../hooks/useTypedSelector';
import { useActions } from '../../hooks/useActions';

type SidebarState = {
    isOpen: boolean,
    setSidebarState: any,
};

const Navbar = ({ isOpen, setSidebarState }: SidebarState): JSX.Element => {
    const { categoriesList } = useTypedSelector((state) => state.categoriesState);
    const { isGameStarted } = useTypedSelector((state) => state.gameState);
    const showSidebar = () => setSidebarState(!isOpen);
    const { changeGameState } = useActions();
    const [btnClass, setBtnClass] = useState('nav-text');
    const changeState = () => {
        isGameStarted ? changeGameState() : '';
       // btnClass === 'active-link' ? setBtnClass('nav-text') : setBtnClass('active-link');
    }
    return (
        <>
            <div className="navbar" >
                <div className="menu-bars">
                    <FaIcons.FaBars onClick={showSidebar} />
                </div>
                <Switcher />
            </div>

            <nav className={isOpen ? 'nav-menu active' : 'nav-menu'} onClick={e => e.stopPropagation()}>
                <ul className='nav-menu-items' onClick={showSidebar}>
                    <li key={100} className="navbar-toggle">
                        <div className="menu-bars">
                            <AiIcons.AiOutlineClose />
                        </div>
                    </li>
                    <li key={101} className={btnClass} onClick={changeState}>
                        <Link to="/">
                            <span>Home</span>
                        </Link>
                    </li>
                    {
                        categoriesList.map((category: { categoryPath: string; categoryName: string; image:string; }, idx: number) => {
                            const { categoryName, categoryPath, image } = category;
                            return (
                                <li key={idx} className={btnClass} onClick={changeState}>
                                    <Link to={categoryPath}>
                                        <span>{categoryName}</span>
                                    </Link>
                                </li>
                            )
                        })
                    }
                </ul>
            </nav>
        </>
    )
};

export default Navbar;
