/* eslint-disable jsx-a11y/no-static-element-interactions */
/* eslint-disable jsx-a11y/control-has-associated-label */
/* eslint-disable jsx-a11y/label-has-associated-control */
import React, { useEffect } from 'react';
import { Link, useHistory } from 'react-router-dom';
import { useActions } from '../../hooks/useActions';
import { useTypedSelector } from '../../hooks/useTypedSelector';
import { store } from '../../store';
import './switcher.scss';

const Switcher = () => {
  const { mode } = useTypedSelector((state) => state.mode);
  const { changeMode } = useActions();

  return (
    <div className="switcher">
      <input type="checkbox" id="switch-checkbox" />
      <label id="button" htmlFor="switch-checkbox">
        { mode === 'train'
        && <div id="knob" onClick={changeMode} />}
        { mode === 'game'
        && <div id="knob-active" onClick={changeMode} />}
        <div id="subscribe">Game</div>
        <div id="alright">Train</div>
      </label>
    </div>
  );
};

export default Switcher;
