/* eslint-disable no-param-reassign */
/* eslint-disable no-plusplus */
import cards from '../../cards';
import { CardsAction, CardsActionTypes, CardsState } from '../../types/cards';

const initialState: CardsState = {
  cardsList: cards,
};

export const CardsReducer = (state = initialState, action: CardsAction): CardsState => {
  switch (action.type) {
    case CardsActionTypes.CHANGE_CARDS_STATE:
      for (let i = 0; i < state.cardsList.length; i++) {
        for (let j = 0; j < state.cardsList[i].length; j++) {
          if (!state.cardsList[i][j].isCardGuessed) {
            state.cardsList[i][j].isCardGuessed = (state.cardsList[i][j].word === action.payload);
          }
        }
      }
      return {
        ...state,
      };
    default:
      return state;
  }
};
