/* eslint-disable jsx-a11y/click-events-have-key-events */
/* eslint-disable jsx-a11y/no-static-element-interactions */
import React, { useEffect, useState } from 'react';
import Navbar from '../../components/navbar/Navbar';
import { useActions } from '../../hooks/useActions';
import { useTypedSelector } from '../../hooks/useTypedSelector';
import Cards from '../main/components/cardsContainer/Cards';
import CategoryContainer from '../main/components/categoriesContainer/categoryContainer';
import * as Imgs from '../../resources/img';
import './pageBuilder.scss';
import Footer from '../../components/footer/Footer';

type Props = {
  // cards: Array<{
  //   categoryPath: string,
  //   categoryName: string;
  //   image: string;
  // }>,
  pageType: string,
  id: number,
}

const PageBuilder = ({
  pageType, id,
}: Props) => {
  const { mode } = useTypedSelector((state) => state.mode);
  const { movesNum } = useTypedSelector((state) => state.movesState);
  const { failures } = useTypedSelector((state) => state.movesState);
  const [isSidebarOpen, setSidebarState] = useState(false);
  const failureImg = Imgs.failurePic;
  const dancing = Imgs.dancingePic;
  const { changeMovesNumber } = useActions();
  const { changeFailuresNumber } = useActions();

  const closeSidebar = () => {
    setSidebarState(false);
  };

  return (
    <div className="page">
      <Navbar isOpen={isSidebarOpen} setSidebarState={setSidebarState} />
      { pageType === 'categories'
      && (
      <div className="main-page" onClick={closeSidebar}>
        <CategoryContainer />
      </div>
      )}
      { pageType === 'cards'
      && (
      <div className="category-page" onClick={closeSidebar}>
        <Cards categoryId={id} />
      </div>
      )}
      { pageType === 'final-page'
      && (
      <div className="final-page" onClick={closeSidebar}>
        { failures > 0 ? (
          <div className="result-wrapper">
            <img src={Imgs.failurePic} alt="Failure" />
            <p>
              You have done
              {' '}
              {failures}
              {' '}
              mistake(s)!
            </p>
          </div>
        ) : (
          <div className="result-wrapper">
            <img src={Imgs.dancingePic} alt="Failure" />
            <p>
              Congrats!
              You are real winner today!
            </p>
          </div>
        )}
      </div>
      )}
      <Footer />
    </div>
  );
};

export default PageBuilder;
